import React, { Component } from 'react';

import { TOPBAR_HEIGHT } from './constants'

import { Modal, Button, Divider, Popover, Icon } from 'antd';

import { get, set } from 'idb-keyval';

import itdp from './img/itdp.png';
import ucb from './img/ucb.png';
import logo from './img/logo_green.svg';

import './TopBar.css'

class TopBar extends Component {
    info() {
        Modal.info({
            title: 'Sobre',
            className: 'about-modal',
            content: (
                <div>
                    <p>
                        A plataforma CicloMapa √© uma ferramenta que buscar ampliar a visibilidade das infraestruturas ciclovi√°rias mapeadas no OpenStreetMap e engajar uma comunidade de mapeadores para atualiza√ß√£o colaborativa de dados e incid√™ncia em pol√≠ticas p√∫blicas de mobilidade urbana.
                    </p>

                    <p>
                        Este √© um projeto Open Source, e seu c√≥digo pode ser encontrado no <a href="https://github.com/cmdalbem/ciclomapa/">GitHub</a>.
                    </p>

                    <div>
                        Apoio:

                        <div className="logos">
                            <a href="http://itdpbrasil.org" target="_BLANK" rel="noopener noreferrer">
                                <img src={itdp} alt="Logo do ITDP"></img>
                            </a>
                            <a href="https://www.uniaodeciclistas.org.br/" target="_BLANK" rel="noopener noreferrer">
                                <img src={ucb} alt="Logo da UCB"></img>
                            </a>
                        </div>
                    </div>
                </div>
            ),
            onOk() { },
        });
    }

    showCityPicker() {
        let body = document.querySelector('body');
        body.classList.add('show-city-picker');
        body.querySelector('.mapboxgl-ctrl-top-left .mapboxgl-ctrl-geocoder input').focus();
    }

    componentDidMount() {
        get('hasSeenWelcomeMsg')
                .then(data => {
                    if (!data) {
                        this.info();
                        set('hasSeenWelcomeMsg', true);
                    }
                });
    }

    render() {
        const parts = this.props.title.split(',');
        const city = parts[0], 
            state = parts[1];
            // country = parts[2];
        let updatedAt;

        // Super special case for Brasilia because it's so damn big and we're not loading the data into memory
        const isBrasilia = city === 'Bras√≠lia';
        
        if (this.props.lastUpdate) {
            updatedAt = this.props.lastUpdate.toLocaleString('pt-BR');
        }
        
        return (
            <div className="topbar" style={{height: TOPBAR_HEIGHT}}>
                <h1>
                    <img id="logo" src={logo} alt="CICLOMAPA"></img>
                </h1>

                <div>
                    <Button 
                        size="large"
                        onClick={this.showCityPicker}
                    >
                        <h3 className="areaname">
                            <span className="state">
                                {state}
                            </span>
                            <span className="city">
                                {city}
                            </span> 

                            <Icon
                                type="down"
                                size="small"
                                style={{ fontSize: '16px', color: '#4ba96e' }}
                            >
                            </Icon>
                        </h3>
                    </Button>

                    {
                        updatedAt &&
                        <Popover
                            placement="bottom"
                            content={(
                                <div style={{maxWidth: 300}}>
                                    <div>
                                        Dados obtidos do OpenStreetMaps em <b>{updatedAt}</b>.
                                    </div>

                                    <Button
                                        size="small"
                                        icon="redo"
                                        type="danger" ghost
                                        onClick={this.props.forceUpdate}
                                        disabled={isBrasilia}
                                    >
                                        Atualizar
                                    </Button>
                                    
                                    {
                                        isBrasilia &&
                                            <div style={{fontStyle: 'italic', fontSize: ''}}>
                                                Esta cidade n√£o pode ser atualizada automaticamente.
                                            </div>
                                    }
                                </div>
                            )}
                            arrowPointAtCenter={true}
                        >
                            <Icon type="info-circle" style={{ marginLeft: '8px' }}/>
                        </Popover>
                    }
                </div>
                
                <div>
                    <Button
                        size="large"
                        type="link"
                        onClick={this.info}
                    >
                        Sobre
                    </Button>

                    <Divider type="vertical" />

                    <Button
                        size="large"
                        type="link"
                        onClick={this.props.downloadData}
                        disabled={isBrasilia}
                        style={{ opacity: isBrasilia ? .5 : 1}}
                    >
                        <Icon type="download" /> Dados
                    </Button>
                </div>
            </div>
        )
    }
}

export default TopBar;